import { Panel, PanelHeader, Button, Div, Group, SimpleCell, Header } from '@vkontakte/vkui';
import { Icon28PhoneOutline, Icon28MailOutline } from '@vkontakte/icons';
import { useEffect, useState } from 'react';
import declOfnum from '../utils/declOfnum'
import formatNum from '../utils/formatNum'

import './Result.css'

const Result = ({ id, go, results }) => {
    const [date, setDate] = useState(Date.now())
    const [old, setOld] = useState(0)

    useEffect(() => {
        let dat = new Date(results.date.year + '-' + results.date.month + '-' + results.date.day)
        setOld(date - dat)
    }, [date])

return  <Panel id={id}>
			<PanelHeader>Результат</PanelHeader>
            <Div>
                <div className='result-menu'>
                <div className='result-card'>
                    <h3>ТЫ ЖИВЕШЬ</h3>
                    <p>ТЕБЕ <span className='span-info'>{formatNum((old / 31536000000).toFixed(0))}</span> {declOfnum((old / 31536000000).toFixed(0), ['ГОД', 'ГОДА', 'ЛЕТ'])}</p>
                    <p>ИЛИ <span className='span-info'>{formatNum((old / 86400000).toFixed(0))}</span> {declOfnum((old / 86400000).toFixed(0), ['ДЕНЬ', 'ДНЯ', 'ДНЕЙ'])}</p>
                    <p>ИЛИ <span className='span-info'>{formatNum((old / 3600000).toFixed(0))}</span> {declOfnum((old / 3600000).toFixed(0), ['ЧАС', 'ЧАСА', 'ЧАСОВ'])}</p>
                    <p>ИЛИ <span className='span-info'>{formatNum((old / 60000).toFixed(0))}</span> {declOfnum((old / 60000).toFixed(0), ['МИНУТА', 'МИНУТЫ', 'МИНУТ'])}</p>
                    <p>ИЛИ <span className='span-info'>{formatNum((old / 1000).toFixed(0))}</span> {declOfnum((old / 1000).toFixed(0), ['СЕКУНДА', 'СЕКУНДЫ', 'СЕКУНД'])}</p>
                </div>
                <div className='result-card sleep'>
                    <h3>СОН</h3>
                    <p>СПАЛ <span className='span-info'>{formatNum((old * 0.3 / 31536000000).toFixed(0))}</span> {declOfnum((old * 0.3 / 31536000000).toFixed(0), ['ГОД', 'ГОДА', 'ЛЕТ'])}</p>
                    <p>ИЛИ <span className='span-info'>{formatNum((old * 0.3 / 86400000).toFixed(0))} </span>{declOfnum((old * 0.3 / 86400000).toFixed(0), ['ДЕНЬ', 'ДНЯ', 'ДНЕЙ'])}</p>
                    <p>ИЛИ <span className='span-info'>{formatNum((old * 0.3 / 3600000).toFixed(0))}</span> {declOfnum((old * 0.3 / 3600000).toFixed(0), ['ЧАС', 'ЧАСА', 'ЧАСОВ'])}</p>
                </div>
                <div className='result-card eyes'>
                    <h3>МАРГНУЛ</h3>
                    <p>ПРИМЕРНО <span className='span-info'>{formatNum((old / 8000).toFixed(0))}</span> РАЗ</p>
                </div>
                </div>
                
                <Button 
                    style={{marginTop: 20}}
				    stretched
				    align='center'
				    size='l'
				    mode='outline'
				    onClick={() => {
                        go({currentTarget: { dataset: { to: 'home'}}})
		    		}}>
				    Повторить
			    </Button>
            </Div>
        </Panel>
}

export default Result